define([
  "core/templates",
  "core/notification",
  "core/modal",
  "core/custom_interaction_events",
  "core/str",
  "local_datacurso/repository/chatbot",
], (Templates, Notification, Modal, CustomEvents, Str, chatbotRepository) => {
  const LINK_SELECTOR = '[data-action="local_datacurso/add_activity_ai"]';

  let modal = null;
  let initialized = false;

  const init = () => {
    if (initialized) {
      return;
    }
    initialized = true;

    const events = [
      "click",
      CustomEvents.events.activate,
      CustomEvents.events.keyboardActivate,
    ];

    CustomEvents.define(document, events);

    events.forEach((event) => {
      document.addEventListener(event, async (e) => {
        const link = e.target.closest(LINK_SELECTOR);
        if (!link) {
          return;
        }
        e.preventDefault();
        const payload = readDataset(link);
        await openChatModal(payload);
      });
    });
  };

  const readDataset = (el) => {
    const { sectionnum, beforemod } = el.dataset;
    return {
      sectionnum: Number(sectionnum),
      beforemod: beforemod ? Number(beforemod) : null,
    };
  };

  const openChatModal = async (payload) => {
    try {
      // Si ya hay un modal abierto, cerrarlo primero
      if (modal) {
        await modal.destroy();
        modal = null;
      }

      const bodyHTML = await Templates.render(
        "local_datacurso/add_activity_ai_modal",
        {}
      );

      const title = await Str.get_string(
        "addactivityai_modaltitle",
        "local_datacurso"
      );

      modal = await Modal.create({
        title,
        large: true,
        show: true,
        removeOnClose: true,
      });

      // Manejar el evento de cierre del modal
      modal.getRoot().on("hidden.bs.modal", () => {
        if (modal) {
          modal.destroy();
          modal = null;
        }
      });

      await modal.setBody(bodyHTML);
      // No llamar a show() nuevamente; el modal ya fue creado con show: true.

      // Handlers del chat
      const bodyEl = modal.getBody()[0];
      wireChatHandlers(bodyEl, payload);
    } catch (err) {
      Notification.exception(err);
    }
  };

  const wireChatHandlers = (container, payload) => {
    const messagesEl = container.querySelector(".bdai-messages");
    const form = container.querySelector("form.bdai-input");
    const textarea = form.querySelector("textarea");
    const sendBtn = form.querySelector(".bdai-send");

    // Mensaje de bienvenida.
    Str.get_string("addactivityai_welcome", "local_datacurso").then((s) => {
      pushAI(messagesEl, s);
    });

    // Enviar con submit.
    form.addEventListener("submit", async (e) => {
      e.preventDefault();
      const prompt = textarea.value.trim();
      if (!prompt) return;

      const generateImages = document.querySelector('input[name="generate_images"]:checked').value;

      pushUser(messagesEl, prompt);
      textarea.value = "";
      textarea.focus();

      // Disable send button.
      sendBtn.disabled = true;
      // Disable textarea.
      textarea.disabled = true;
      // Disable radio buttons.
      const radioButtons = document.querySelectorAll('input[name="generate_images"]');
      radioButtons.forEach((rb) => {
        rb.disabled = true;
      });
      setLoading(sendBtn, true);
      const typing = pushTyping(messagesEl);
      try {
        const response = await chatbotRepository.createMod({
          ...payload,
          prompt,
          generateimages: generateImages
        });
        if (!response.ok) {
          throw new Error(response.message);
        }

        removeTyping(typing);
        renderWSResult(messagesEl, response);
        // Mostrar opciones para crear otra actividad o finalizar.
        await showNextActionButtons(messagesEl, response?.courseurl, {
          onCreateAnother: () => {
            // Enfocar en el textarea para que el usuario pueda escribir otra petición.
            textarea.focus();
          },
        });
      } catch (err) {
        removeTyping(typing);

        // This is when cloudflare timeout.
        if (!err) {
          const msg = await Str.get_string("resourcecreatedsuccess", "local_datacurso");
          pushAI(messagesEl, msg);
          // No redirigir automáticamente. Mostrar opciones.
          await showNextActionButtons(messagesEl, null, {
            onCreateAnother: () => {
              textarea.focus();
            },
          });
        } else {
          // Other errors.
          Str.get_string("addactivityai_error", "local_datacurso").then((s) => {
            pushAI(messagesEl, `❌ ${s}`);
          });
        }
      } finally {
        radioButtons.forEach((rb) => {
          rb.disabled = false;
        });
        textarea.disabled = false;
        sendBtn.disabled = false;
        setLoading(sendBtn, false);
        scrollToBottom(messagesEl);
      }
    });

    // Enter para enviar (sin Ctrl, ya que es más común en chat)
    textarea.addEventListener("keydown", (e) => {
      if (e.key === "Enter" && !e.shiftKey) {
        e.preventDefault();
        form.requestSubmit();
      }
    });
  };

  const pushUser = (wrap, text) => addBubble(wrap, text, "user");
  const pushAI = (wrap, text) => addBubble(wrap, text, "ai");

  const addBubble = (wrap, text, role) => {
    const row = document.createElement("div");
    row.className = `bdai-msg ${role}`;
    const b = document.createElement("div");
    b.className = "bubble";
    b.textContent = text;
    row.appendChild(b);
    wrap.appendChild(row);
    scrollToBottom(wrap);
  };

  const pushTyping = (wrap) => {
    const row = document.createElement("div");
    row.className = "bdai-msg ai bdai-typing";
    const b = document.createElement("div");
    b.className = "bubble";
    b.innerHTML =
      '<span class="dot"></span><span class="dot"></span><span class="dot"></span>';
    row.appendChild(b);
    wrap.appendChild(row);
    scrollToBottom(wrap);
    return row;
  };

  const removeTyping = (el) => el?.remove();

  const scrollToBottom = (wrap) => {
    wrap.scrollTop = wrap.scrollHeight;
  };

  const setLoading = (btn, isLoading) => {
    btn.disabled = isLoading;
    btn.style.opacity = isLoading ? 0.7 : 1;
  };

  const renderWSResult = (wrap, res) => {
    const lines = [];
    if (res?.success === false) {
      if (res?.message) {
        pushAI(wrap, res.message);
      } else {
        Str.get_string("addactivityai_faildefault", "local_datacurso").then(
          (s) => pushAI(wrap, s)
        );
      }
      return;
    }

    if (res?.message) lines.push(res.message);

    if (lines.length) {
      pushAI(wrap, lines.join("\n"));
      setTimeout(() => {
        const last = wrap.querySelector(".bdai-msg.ai:last-child .bubble");
        if (last) last.textContent = lines.join("\n");
      }, 50);
    } else {
      Str.get_string("addactivityai_done", "local_datacurso").then((s) =>
        pushAI(wrap, s)
      );
    }
  };

  // Muestra botones de siguiente acción: crear otra actividad (sí) o finalizar (no), usando template Mustache.
  const showNextActionButtons = async (wrap, courseurl, { onCreateAnother } = {}) => {
    // Evitar duplicar el conjunto de botones si ya se añadieron.
    if (wrap.querySelector('.bdai-next-actions')) {
      return;
    }

    try {
      const tpl = await Templates.renderForPromise('local_datacurso/add_activity_ai_next_actions', {});
      const {html, js} = tpl;
      // Insertar el HTML en el contenedor usando la API de Moodle, incluyendo JS del template si lo hubiera.
      await Templates.appendNodeContents(wrap, html, js);
      // Obtener el nodo recién insertado (la última ocurrencia por clase).
      const nodes = wrap.querySelectorAll('.bdai-next-actions');
      const node = nodes[nodes.length - 1];

      const yesBtn = node?.querySelector('[data-action="bdai-create-another"]');
      const noBtn = node?.querySelector('[data-action="bdai-finish"]');

      if (yesBtn) {
        yesBtn.addEventListener('click', () => {
          node.remove();
          if (typeof onCreateAnother === 'function') {
            onCreateAnother();
          }
          scrollToBottom(wrap);
        });
      }

      if (noBtn) {
        noBtn.addEventListener('click', () => {
          window.location.reload();
        });
      }

      scrollToBottom(wrap);
    } catch (e) {
      // Si algo falla al renderizar el template, no detiene el flujo; simplemente no muestra los botones.
      // Notificamos el error para facilitar el diagnóstico en caso de que no se muestren los botones.
      Notification.exception(e);
    }
  };

  return { init, openChatModal };
});
